import http from "./request"

export default{
    //
    getReformList(data){
        return http.post("/SafetyRectification/list",data)
    },
    //
    getReformDetails(data){
        return http.post("/SafetyRectification/details",data)
    },
    //
    getSelf(data){
        return http.post("SafetyRectification/Reply",data)
    },
    
}


